<template>
    <div>
        <ul class="nav">
            <li v-for="(item,index) of cities" :key="index" @click="handleClick">{{index}}</li>
        </ul>
    </div>
</template>
<script>
export default {
    name:"CityWord",
    props:['cities'],
    methods:{
        handleClick(event){
           this.$store.commit("chengLetter",event.target.innerText)
        }
    }
}
</script>
